import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {Router, ActivatedRoute} from '@angular/router';
import {CurrencyPipe, Location} from '@angular/common';
import {Constants} from '../../app.constants';
import Swal from 'sweetalert2';
import {ILogs} from '../../interfaces/interfaces';
import {SystemLogsService} from '../../services/system-logs.service';


@Component({
    selector: 'app-system-logs',
    templateUrl: './system-logs.component.html',
    encapsulation: ViewEncapsulation.None,
})
export class SystemLogsComponent implements OnInit {
    public logs: ILogs[] = [];
    selectedLog: ILogs[];
    loading = false;
    model: any = {};
    checked = false;
    errorMessage = '';

    current_page: number;
    data: ILogs[];
    from: number;
    last_page: number;
    next_page_url: string;
    path: string;
    per_page: number;
    prev_page_url: string;
    to: number;
    total: number;

    constructor(public systemLogsService: SystemLogsService, private _Location: Location,
                private route: ActivatedRoute,
                private router: Router) {
    }

    ngOnInit() {
        this.doSearch();
    }

    doSearch() {
        this.loading = true;
        const params = [];
        if (this.model.hasOwnProperty('from')) {
            const fromDate = this.model.from.year + '-' + this.model.from.month + '-' + this.model.from.day;
            params.push('from=' + fromDate);
        }
        if (this.model.hasOwnProperty('to')) {
            const toDate = this.model.to.year + '-' + this.model.to.month + '-' + this.model.to.day
            params.push('to=' + toDate);
        }


        this.loadData('', params.join('&'));
    }

    loadData(url, params) {
        this.logs = [];
        this.last_page = -1;
        this.loading = true;
        this.errorMessage = '';
        this.systemLogsService.searchLogs(url, params).subscribe((data) => {
            this.logs = data.data;
            this.current_page = data.current_page;
            this.from = data.from;
            this.last_page = data.last_page;
            this.next_page_url = data.next_page_url;
            this.path = data.path;
            this.prev_page_url = data.prev_page_url;
            this.per_page = data.per_page;
            this.to = data.to;
            this.total = data.total;
            this.loading = false;
        }, (err) => {
            this.errorMessage = Constants.handleError(err, this.router);
            this.loading = false;
        })
    }

    deleteLogSelected() {
        // swal('Are You Sure  You want delete these?', 'You won\'t be able to revert this!', 'warning', 'dangerMode: true');
        Swal.fire({
            title: 'Delete selected items?',
            text: 'You won\'t be able to revert this!',
            type: 'question',
            cancelButtonText: 'Cancel',
            showCancelButton: true,
            confirmButtonText: 'Yes Delete',
            showConfirmButton: true
        }).then(willDelete => {
            if (willDelete.value) {
                this.selectedLog = this.logs.filter(_ => _.selected);
                this.selectedLog.forEach((log) => {
                    this.systemLogsService.RemoveLog(log.id).subscribe(data => {
                            if (data !== -1) {
                            }
                        }
                    );
                });
                // swal('Deleted!', 'Log(s) has been deleted!', 'success');
                this.router.navigate(['/app/system-logs'])
            }
        });
    }

    deleteLog(id, i) {
        Swal.fire({
            title: 'Are you sure?',
            text: 'You won\'t be able to revert this!',
            type: 'question',
            cancelButtonText: 'Cancel',
            showCancelButton: true,
            confirmButtonText: 'Yes Delete',
            showConfirmButton: true
        }).then(willDelete => {
            if (willDelete.value) {
                this.systemLogsService.deleteLog(id).subscribe((status: boolean) => {
                        if (status) {
                            Constants.succeeded('Log has been deleted!')
                            window.setTimeout(function () {
                                location.reload();
                            }, 2000)
                        } else {
                            this.errorMessage = 'Unable to delete Log'
                        }
                        this.loading = false;
                    }, (err) => {
                        this.loading = false;
                        this.errorMessage = Constants.handleError(err, this.router);
                    }
                );
            }
        })
    }

    checkAll(ev) {
        this.logs.forEach(x => x.selected = ev.target.checked)
    }

    isAllChecked() {
        if (this.logs === null || !this.logs || this.logs.length < 1) {
            return false
        }

        return this.logs.every(_ => _.selected);
    }

}
